.coin-container{
    display: flex;
    justify-content: center;
    align-items:var(-webkit-backdrop-filter);
    bottom: 2ch;
    shape-rendering: geometricPrecision;
}


.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height:80px;
    border-bottom: 2px solid rgb(255, 255, 255) ;
    width: 1200px;
}
.coin{
    display: flex;
    align-items: center;
    padding-right: 44px;
    top: 45px;
    min-width: 100px;
}
.coin h1{
    font-size: 20px;
    width: 124px;
}
.coin img{
    height: 30px;
    width: 30px;
    margin-right:20px;
}
.coin-symbol{
    text-transform: uppercase;
    width: 45px;
    margin: right 10px;;
    

}
.coin-data{
    display: flex;
    text-align:center;
    justify-content: space-between;
    width: 90%;
}
.coin-price{

width: 300px;
}
.coin-volume{
    width: 300px;
    align-items: center;
}
.coin-marketcap{
    width: 500px;
    align-items: center;
}
.red{
    color: tomato;
}
.green{
    color:green;
}