@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1a;
  color: rgb(255, 255, 255);
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  color:white;
}

.coin-search{
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.coin-text{
  margin-bottom: 32px;
  text-align:center;
}
.coin-input {
  width: 300px;
  height: auto;
  padding: 1rem;
  border-radius: 1rem;
  background: rgba(0, 0, 0, 0.993);
  box-shadow: 0 8px 32px 0 rgb(41, 41, 41);
  backdrop-filter: blur( 1px );
  -webkit-backdrop-filter: blur( 2px );
  border-radius: 100px;
  border: 1px solid rgba(0, 0, 0, 0.18);
}

.coin-input::placeholder {
  color: #e2e2e2;
}